import styled from "styled-components";
import { AccountIco, HomeIco, MenuIco } from "../../../assets/icon/Svg";
import { useRecoilState } from "recoil";
import menuAtom from "../../../recoil/menuAtom";
import { useEffect, useState } from "react";
import { NavLink, useLocation, useNavigate, useNavigation } from "react-router-dom";

const TitleWrap = styled.div`
  width: 100%;
  display: flex;
  text-align: center;
  color: #F7F7F7;
  align-items: center;
  margin-top: 10px;
  padding: 0 20px;
  justify-content: space-between;
`

const TitleText = styled.p`
  font-size: 17px;
  font-weight: 500;
  color: #fff;
`

const InfoWrap = styled.div`
  svg{
    position: relative;
    top: 50%;
    flex: 1;
    fill: #F7F7F7;
    justify-content: center;
    margin-right: 10px;
    height: 22px;
  }
`

const Title = () => {
  const [menuState, setMenuState] = useRecoilState(menuAtom)
  const [pageName, setpageName] = useState("")

  const location = useLocation();

  const currentPage = [
    {
      pathname: '/',
      title : ''
    },
    {
      pathname: '/receipt',
      title : ''
    },
    {
      pathname: '/regis',
      title : ''
    },
    {
      pathname: '/inventory',
      title : ''
    },
    {
      pathname: '/inventory/request',
      title : ''
    },
    {
      pathname: '/inventory/wait',
      title : ''
    },
    {
      pathname: '/report',
      title : ''
    },
    {
      pathname: '/sale',
      title : ''
    },
  ]

  const getPageName = () => {
    for(let i = 0; i < currentPage.length; i++) {
      if (currentPage[i].pathname === location.pathname) {
        setpageName(currentPage[i].title);
      } 
    }
  }
  useEffect(() => {
    getPageName();
  }, [location.pathname]);

  return <TitleWrap>

    <InfoWrap>
      <NavLink to="/">
        <HomeIco />
      </NavLink>
    </InfoWrap>

    <TitleText> {pageName} </TitleText>

    <InfoWrap>
      <AccountIco />
      <button onClick={() => setMenuState({ isOpen: !menuState.isOpen })}>
      <MenuIco />
      </button>
    </InfoWrap>

  </TitleWrap>
}

export default Title
